// colors
$orange: #ec6b06;
$black: #33353a;
$grey: #474950;
$grey-light: #e3e5e9;
$grey-simple: #9A9999;
$grey-bg: #E5E6E9;
$grey-green: #F8F9F9;
$black2: #000000;

// breakpoints
@mixin desktop-less-xl {
    @include r(1919) {
        @content; } }
@mixin desktop-less-lg {
    @include r(1679) {
        @content; } }
@mixin desktop-less-md {
    @include r(1279) {
        @content; } }
@mixin desktop-less-sm {
    @include r(1023) {
        @content; } }
@mixin tablet-less-lg {
    @include r(990) {
        @content; } }
@mixin tablet-less-md {
    @include r(767) {
        @content; } }
@mixin tablet-less-sm {
    @include r(719) {
        @content; } }
@mixin mob-less-lg {
    @include r(599) {
        @content; } }
@mixin mob-less-md {
    @include r(374) {
        @content; } }

@mixin tablet-less-lg-land {
    @include rorient(1024, landscape) {
        @content; } }
@mixin tablet-less-lg-port {
    @include rorient(768, portrait) {
        @content; } }
@mixin mob-less-lg-land {
    @include rorient(812, landscape) {
        @content; } }
@mixin mob-less-lg-port {
    @include rorient(812, portrait) {
        @content; } }
@mixin mob-less-md-land {
    @include rorient(736, landscape) {
        @content; } }
@mixin mob-less-md-port {
    @include rorient(736, portrait) {
        @content; } }
@mixin mob-less-sm-land {
    @include rorient(667, landscape) {
        @content; } }
@mixin mob-less-sm-port {
    @include rorient(667, portrait) {
        @content; } }
@mixin mob-less-xs-land {
    @include rorient(568, landscape) {
        @content; } }
@mixin mob-less-xs-port {
    @include rorient(568, portrait) {
        @content; } }
