@mixin hm-l {
    // font-family: 'Hind Madurai Light', Arial, Helvetica, sans-serif
    font-family: 'Hind Madurai', Arial, Helvetica, sans-serif;
    font-weight: 300; }
@mixin hm-r {
    // font-family: 'Hind Madurai Regular', Arial, Helvetica, sans-serif
    font-family: 'Hind Madurai', Arial, Helvetica, sans-serif;
    font-weight: 400; }
@mixin hm-m {
    // font-family: 'Hind Madurai Medium', Arial, Helvetica, sans-serif
    font-family: 'Hind Madurai', Arial, Helvetica, sans-serif;
    font-weight: 500; }
@mixin hm-sb {
    // font-family: 'Hind Madurai Semibold', Arial, Helvetica, sans-serif
    font-family: 'Hind Madurai', Arial, Helvetica, sans-serif;
    font-weight: 600; }
@mixin hm-b {
    // font-family: 'Hind Madurai Bold', Arial, Helvetica, sans-serif
    font-family: 'Hind Madurai', Arial, Helvetica, sans-serif;
    font-weight: 700; }
